/*-----------------------------------------------------------------------------------------------------------------
LAYOUTS
 ------------------------------------------------------------------------------------------------------------------*/
.fdc{
	display: flex;
	flex-direction: column;
}
.fdr{
	display: flex;
	flex-direction: row;
}

 .gap-20,
.gap-x-20 {	
	gap: 0 20px;
}
.gap-20,
.gap-y-20 {
	gap: 20px 0;
}

.gap-30,
.gap-x-30 {
	gap: 0 30px;
}

.gap-30,
.gap-y-30 {
	gap: 30px 0;
}

.gap-40,
.gap-x-40 {
	gap: 0 40px;
}

.gap-40,
.gap-y-40 {
	gap: 40px 0;
}

.column_66 {
	flex-shrink: 0;
	width: 66.6%;

	display: flex;
	flex-direction: column;

	&--spaceborder {
		padding-bottom: 30px;
		border-bottom: 1px solid rgb(0, 0, 0, 0.1);
	}
}
.column_33 {
	flex-shrink: 0;
	width: 33.3%;

	display: flex;
	flex-direction: column;
}

